*{
    font-family: Arial, Helvetica, sans-serif;
}
body {
    padding: 60px;

}

input {
    color: grey;
    font-size: 1.2rem;
    width: 100%;
}

h1 {
    color: brown;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 6px;
    overflow: hidden;


}

.card img {
    max-height: 170px;
}

.card p {
    padding: 8px;
    text-align: center;

}